/**
 * queryParams
 * -filterList
 * -searchText
 * -sortColumn
 * -sortDirection
 * -page
 * -rowsPerPage
 * -count
 * 
 * GETquery-strings
 * filterListPOST
 **/
export const manualsURL = '/api/v1/manuals';

export interface ManualsQueryParams {
  filters: {
    favorite: Show;
    like: Show;
    categoryId: string | null;
  },
  searchText: string[];
  sortColumn: string;
  sortDirection: 'asc' | 'desc';
  page: number;
  rowsPerPage: number;
}

export enum Show { ALL, CHECK, UNCHECK }

export const baseManualQueryParams: ManualsQueryParams = {
  filters: {
    favorite: Show.ALL,
    like: Show.ALL,
    categoryId: null,
  },
  searchText: [],
  sortColumn: 'updateAt',
  sortDirection: 'desc',
  page: 0,
  rowsPerPage: 10
}

// QueryParams
export interface ManualsQueryResponse {
  queryParams: ManualsQueryParams;
  manuals: Manual[];
  count: number;
}

